// app/components/AdminGuard.tsx - VERSION CORRECTE ET FONCTIONNELLE
'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'

interface AdminGuardProps {
  children: React.ReactNode
}

export default function AdminGuard({ children }: AdminGuardProps) {
  const router = useRouter()
  const [isChecking, setIsChecking] = useState(true)

  useEffect(() => {
    const checkAdminAccess = () => {
      console.log('üîê AdminGuard - V√©rification des permissions...')
      
      // 1. V√©rifier si le token existe
      const token = localStorage.getItem('access_token')
      if (!token) {
        console.log('‚ùå Pas de token JWT, redirection vers /login')
        router.push('/login?redirect=/admin')
        return false
      }

      // 2. V√©rifier les donn√©es membre
      const memberStr = localStorage.getItem('member')
      if (!memberStr) {
        console.log('‚ùå Pas de donn√©es membre, redirection vers /')
        router.push('/')
        return false
      }

      try {
        // 3. Parser et v√©rifier le r√¥le
        const member = JSON.parse(memberStr)
        console.log('üë§ R√¥le d√©tect√©:', member.role)
        
        const isAdmin = member.role === 'ADMIN' || member.role === 'WEBMASTER'
        
        if (!isAdmin) {
          console.log('‚ùå R√¥le insuffisant pour acc√©der √† l\'admin')
          router.push('/')
          return false
        }

        // 4. Si tout est bon
        console.log('‚úÖ AdminGuard - Acc√®s autoris√©!')
        return true
        
      } catch (error) {
        console.error('üí• Erreur de v√©rification:', error)
        router.push('/login')
        return false
      }
    }

    // Attendre un peu pour √™tre s√ªr que le localStorage est rempli
    const timer = setTimeout(() => {
      const hasAccess = checkAdminAccess()
      if (hasAccess) {
        setIsChecking(false)
      }
    }, 100)

    return () => clearTimeout(timer)
  }, [router])

  // Pendant la v√©rification
  if (isChecking) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">V√©rification des permissions administrateur...</p>
        </div>
      </div>
    )
  }

  // Si on arrive ici, l'acc√®s est autoris√©
  return <>{children}</>
}